<template>
	<view class="register">
        <u-alert-tips style="margin: 8rpx 20rpx 0 20rpx;" :show="showtip" type="warning" @close="showtip = false"
        	:title="title" :close-able="true" :show-icon="true"></u-alert-tips>
		<view class="formBox" style="margin-top: 20rpx;" v-show="show==1">
			<view class="mititle">
				<image style="width: 59rpx;margin-right: 23rpx;border-radius: 50%;" mode="widthFix" src="/static/img/xssb.png"></image>
				<view style="width: 46%;">谁可以看</view>
				<view class="blue" @click="sushe_show=true">{{txt}}<u-icon name="arrow-right" style="margin-left:17rpx;"></u-icon></view>
			</view>
			<view  style="background: #fff;padding: 0 30rpx;">
			<u-form :model="form" ref="uForm" :error-type="errorType">
				<u-form-item label="动态描述" label-position="top" prop="content" required label-width='140'>
					<u-input v-model="form.content" type="textarea" :border="true" height="237" maxlength="200"/>
				</u-form-item>
			</u-form>
			<u-upload :max-size="10 * 1024 * 1024" max-count="9" :action="action" :file-list="fileList"
			:header="header" @on-remove="onRemove" @on-success="onSuccess"></u-upload>
			<view class="taglist">
				<u-tag :text="item.label" mode="dark" closeable @close="closehd(index)"
				v-for="(item,index) in hdList" :key="index"/>
				<u-tag text="+ 关联活动" @click="bmshow=true"/>
				</view>
			</view>
			<u-button @click="submit" type="primary" style="margin: 50rpx 30rpx;">发布</u-button>
		</view>
		<u-select v-model="sushe_show" mode="single-column" :list="susheArr" @confirm="sushe_confirm"
			value-name='dictValue' label-name='dictLabel'></u-select>
		<u-select v-model="bmshow" mode="single-column" :list="bmArr" @confirm="bm_confirm" value-name='id'
			label-name='universalAntiAboutMain26'></u-select>
			<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		saveData,
		getDicts,
		enterAbout
	} from '../../api/index.js'
	import request from '@/api/request.js'
	export default {
		data() {
			return {
				errorType: ['toast'],
				hdList:[],
				imgList:[],
				txt:'',
				header: {
					Authorization: "Bearer " + uni.getStorageSync("token"),
				},
				showtip: true,
				title:"可发布活动影音动态、图文动态信息",
				show:1,
				susheArr: [],
				sushe_show: false,
				bmArr:[],
				bmshow: false,
				action: request.ROOTHTTP+"/file/upload",
				fileList: [],
				form: {
					queryAuthority: '',
					file: '',
					universalAntiAboutFid:'',
					content:'',
				},
				rules: {
					content: [{
						required: true,
						message: '请输入分队简介',
						trigger: ['change', 'blur']
					}]
				}
			}
		},
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		},
		onLoad() {
		   getDicts('query_quthority').then(res => {
		   	if (res.code == 200) {
		   		this.susheArr = res.data;
		   	}
		   })
		   enterAbout({id:uni.getStorageSync('userInfo').id}).then(res => {
		   	if (res.code == 200) {
		   		this.bmArr = res.data;
		   	}
		   })
		},
		methods: {
			onRemove(index) {
				this.imgList.splice(index, 1);
			},
			onSuccess(data, index, lists, name) {
				this.imgList.push(data.data.url);
			},
			sushe_confirm(e) {
				this.txt = e[0].label;
				this.form.queryAuthority = e[0].value;
			},
			bm_confirm(e) {
				let d ={
					label:e[0].label,
					value:e[0].value
				} 
				this.hdList.push(d);
			},
			closehd(index) {
				this.hdList.splice(index, 1);
			},
			submit() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						this.form.file = this.imgList.join();
						let l=[];
						this.hdList.map((item)=>{
							l.push(item.value);
						})
						this.form.universalAntiAboutFid = l.join();
						saveData(this.form).then(res => {
							if (res.code == 200) {
								this.$refs.uToast.show({
									title: '发布动态成功'
								});
								uni.navigateBack({
										delta:1,//返回
									})
							}
						});
					}
				});
			}
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
		width: 100vw;
		height: 100vh;
	}
	.register .title {
		width: 100%;
		text-align: center;
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		padding: 32rpx 0 25rpx 0;
	}

	.mybox {
		width: 100%;
		text-align: center;
	}

	.mybox /deep/ .u-upload {
		justify-content: center;
	}

	.formBox {
	}

	.mititle {
		font-size: 24rpx;
		color: #333333;
		height: 81rpx;
		margin-bottom: 20rpx;
		background-color: #fff;
		display: flex;
		align-items: center;
		padding: 0 30rpx;
		justify-content:space-between;
	}
	.mititle .blue{
		width: 40%;
		display: inline-block;
		text-align: right;
		color: #4490FB;
	}

	.smtitle {
		width: 100%;
		text-align: center;
		font-size: 24rpx;
		color: #B1B1B1;
	}

	.smtitle b {
		color: #FF9D1D;
	}
	.taglist{
		border-top: 1px solid #ECECEC;
		margin-top: 40rpx;
		padding: 26rpx 0;
	}
	.taglist /deep/ .u-tag{
		margin-right: 20rpx;
	}
</style>